<template>
  <div>
    <div :style="{ background: config.bgColor }">主题</div>
  </div>
</template>

<script>
import theme from "../utils/theme";
const initTheme = theme["DEFAULT"];
export default {
  name: "themeSwitch",
  props: {
    theme: {
      type: Object,
      default: () => ({})
    },
    bgColor: {
      type: String,
      default: initTheme.background
    }
  },
  data() {
    return {
      config: {
        bgColor: this.bgColor
      }
    };
  },
  watch: {
    theme(to, from) {
      this.config.bgColor = this.theme.bgColor;
    }
  }
};
</script>



